<template>
  <div>
    <ToolBar>
      <div>
        <el-input
          placeholder="订单号"
          size="small"
          style="width: 280px"
          v-model="searchParams.orderSn"
          clearable
        ></el-input>

        <el-input
          placeholder="商品名"
          size="small"
          style="width: 140px"
          v-model="searchParams.goodsName"
          clearable
        ></el-input>

        <el-button type="success" size="small" @click="refresh()">查询</el-button>
        <el-button type="warning" size="small" @click="clearSearchParams()">重置</el-button>
      </div>
    </ToolBar>
    <el-table
      :data="tableData"
      border
      style="width: 100%"
      :row-style="{height:'8px'}"
      :cell-style="{padding:'0px 0px'}"
    >
      <el-table-column align="center" prop="orderSn" label="订单号" width="280"></el-table-column>
      <el-table-column align="center" prop="goodsName" label="商品名称" width="250"></el-table-column>
      <el-table-column align="center" prop="goodsPrice" label="价格"></el-table-column>
      <el-table-column align="center" prop="buyNumber" label="购买数量"></el-table-column>
      <el-table-column
        align="center"
        prop="isComment"
        label="评论"
        :formatter="orderIsCommentFormmater"
      ></el-table-column>
      <el-table-column align="center" label="图片">
        <div slot-scope="s">
          <el-image style="width: 80px; height: 80px" :src="s.row.goodsPicUrl" fit="fill"></el-image>
        </div>
      </el-table-column>
    </el-table>
    <Pagination
      :params="searchParams"
      :requestFunc="getOrderDetailList"
      ref="pagination"
      @returnData="returnData"
    />
  </div>
</template>

<script>
import { getOrderDetailList } from "@/api/order/orderManage";
import { resetObject } from "@/utils/common";
import { OrderIsCommentEnum } from "@/utils/Immutable";
export default {
  data() {
    return {
      searchParams: {
        orderSn: "",
        goodsName: ""
      },
      orderIsCommentEnum: OrderIsCommentEnum,
      getOrderDetailList: getOrderDetailList,
      titie: "",
      total: 10,
      showEditDialog: false,
      tableData: []
    };
  },
  mounted() {
    let orderSn = this.$route.query.orderSn;
    if (orderSn) {
      this.searchParams.orderSn = orderSn;
      this.refresh();
    }
  },
  methods: {
    orderIsCommentFormmater(a, b, v) {
      return this.orderIsCommentEnum[v];
    },
    clearSearchParams() {
      resetObject(this.searchParams);
      this.refresh();
    },
    refresh() {
      this.$refs.pagination.Refresh(); //分页刷新
      this.showEditDialog = false;
    },
    returnData(data) {
      this.tableData = data;
    },
    edit(row, type) {
      this.titie = type == "recharge" ? "手动充值" : "手动扣款";
      this.showEditDialog = true;
      this.$refs.edit.update(row.id, type);
    }
  }
};
</script>
