import request from "@/utils/request.js";
/**
 * 登录
 * @param params
 * @returns {AxiosPromise}
 */
export function getOrderList(params) {
    return request({
        url: "/apiadmin/order/findlist",
        method: "get",
        params: params //注意：如果是get请求请使用 params: params
    });
}
export function editOrderStatus(params) {
    return request({
        url: "/apiadmin/order/editorderstatus",
        method: "post",
        data: params //注意：如果是get请求请使用 params: params
    });
}

export function getOrderDetailList(params) {
    return request({
        url: "/apiadmin/order/finddetaillist",
        method: "get",
        params: params //注意：如果是get请求请使用 params: params
    });
}

export function statisticOrderNum() {
    return request({
        url: "/apiadmin/order/statisticordernum",
        method: "get",
    });
}